                            <div class="transaction-item" v-for="(transaction, index) in this.scrollTx" :key="index" style="display:flex;width: 100%;border-bottom: 1px solid lightgray;padding: 2%;height: 70px;"> 
                                <div style="width: 33%;display: flex;align-items: center;">
                                    <div style="background-color: rgba(119,131,143,.1);border-radius: 100%;height: 30px;width: 30px;line-height: 30px;text-align: center;font-weight: 600;font-size: small;">Tx</div>
                                    <div class="tx-hash" style="display: flex;align-items: center;"><el-tag type="primary" size="small" style="width: 144px;overflow: hidden;">{{ transaction.tx_hash }}</el-tag></div>
