                                    <!-- <div>To:<el-tag type="primary" size="small">0xdafea492d9c6733ae3</el-tag></div> -->
                                </div>
                                <div style="width: 33%;display: flex;align-items: center;justify-content: center;">
                                    <!-- <div>From:<el-tag type="primary" size="small">0xdafea492d9c6733ae3</el-tag></div> -->
                                    <div style="display: flex;align-items: center;"><el-tag type="primary" size="small" style="width: 144px;overflow: hidden;">{{ transaction.to_account }}</el-tag></div>
                                </div>
                                <!-- <div style="width: 25%;align-items: center;display: flex;justify-content: center;">
                                    <div style="display: flex;align-items: center;"><el-tag type="success" size="small" style="width: 144px;overflow: hidden;">{{  transaction.timeStamp }}</el-tag></div>
                                </div> -->
