</template>
<script type="text/javascript" src="/static/js/echarts.js"></script>
<script>
import TableComponent from "@/components/Table.vue"
    export default {
        name: 'DashBoard',
        data() {
            return {
                ethusd: 1586.36,
                ethbtc: 0.06847,
                ethsupply: undefined,
                activeName: 'TableTab',
                transactions: [],
                scrollTx: [],
                timer: [],
                // dailyTx: 1000,
                aberration: [],
                value1: true,
                blockNumber: 16540597,
                txTimer: undefined,
                speed: [103,152, 130 ,190, 110, 150, 180, 130, 120, 150, 130, 170],
                blockTime: 'Unknown',
                dashboard: {
                    "hour": 734,
                    "minute": 46,
                    "seconds": 1
                }
            }
                // index: 3
            
        },
        props:{
            history: {
                type: Object,
                default: {}
            },
            popper: {
                type: Function,
                default: null
            },
            // change: {
            //     type: Function,
            //     default: null
            // },
            blockTime: {
                type: String,
                default: 'Unknown'
            },
            transactions: {
                type:Array,
                default: []
            },
            // aberration: {
            //     type: Array,
            //     default: []
            // },
            aberration: {
                type: Function,
                default: null
            },
            blockNumber: {
                type: Number,
                default: 16540597
            },
            dashboard: {
                type: Object,
                default: {
                    "hour": 734,
                    "minute": 46,
                    "seconds": 1
                },
            }
      },
      watch: {
            transactions(newTx) {
                this.transactions = newTx
            },
            aberration(newAb) {
                this.aberration = newAb
            },
            blockNumber(newblocknumber){
                this.blockNumber = newblocknumber
            },
            blockTime(newtime) {
                this.blockTime = newtime
            },
            dashboard(newd) {
                this.dashboard = newd
            }

            },
        components: {
            TableComponent
        },
        mounted() {
                // if (this.$refs.table.timer != undefined) clearInterval(this.timer)
                // this.setRateOption(this.transactions)
                // this.setBarOption(data, xAxis)
                this.scrollTx = this.transactions.slice(this.transactions.length - 8, this.transactions.length)
                // this.speed.push(Math.random() * 100 + 100)
                    let xAxis = []
                    for (var i = 0; i < this.speed.length; i++) {
                        xAxis.push("")
                   }
                    this.setBarOption(this.speed, xAxis)
                setInterval(()=>{
                    this.scrollTx = this.transactions.slice(this.transactions.length - 8, this.transactions.length)
                }, 2000)
                this.timer.push(setInterval(()=>{
                    this.speed.push(Math.random() * 100 + 100)
                    let xAxis = []
                    for (var i = 0; i < this.speed.length; i++) {
                        xAxis.push("")
                   }
                    this.setBarOption(this.speed, xAxis)
                    // this.setRateOption(this.transactions)
                }, 1000))
        },
        methods: {
            setBarOption(data, xAxis) {
                var chart_cd2d54c0a4684292ac7e8a2ff22e3996 = echarts.init(
            document.getElementById('bar'), 'white', {renderer: 'canvas'});
        var option_cd2d54c0a4684292ac7e8a2ff22e3996 = {
    "animation": true,
    "animationThreshold": 2000,
    "animationDuration": 1000,
    "animationEasing": "cubicOut",
    "animationDelay": 0,
    "animationDurationUpdate": 300,
    "animationEasingUpdate": "cubicOut",
    "animationDelayUpdate": 0,
    "series": [
    {
            "type": "line",
            "name": "Transaction synchronization",
            "connectNulls": false,
            "symbol": "emptyCircle",
            "symbolSize": 4,
            "color": "skyblue",
            "showSymbol": true,
            "smooth": false,
            "clip": true,
            "step": false,
            "data": data,
            "hoverAnimation": true,
            "lineStyle": {
                "show": true,
                "width": 1,
                "opacity": 1,
                "color": "#d9ecff",
                "curveness": 0,
                "type": "solid"
            },
            "areaStyle": {
                "opacity": 1,
                "color": "#d9ecff"
            },
            "markLine": {
                "silent": false,
                "precision": 2,
                "label": {
                    "show": true,
                    "position": "middle",
                    "margin": 8,
                    "color": "gray",
                    "formatter": function(data) {
                        // console.log(data)
                        return "Average Speed：" + data.value + "tps"
                    }
                },
                "lineStyle": {
                    "show": true,
                    "width": 1,
                    "color": "gray",
                    "curveness": 0,
                    "type": "solid",
                    "opacity": 1
            },
                "data": [
                    {
                        "type": "average",
                        "name": "Average Speed(tps)",
                    }
                ],
                "tooltip": {
                    "show": true,
                    "trigger": "item",
                    "triggerOn": "mousemove|click",
                    "axisPointer": {
                        "type": "line"
                    },
                    "showContent": true,
                    "alwaysShowContent": true,
                    "showDelay": 0,
                    "hideDelay": 100,
                    "textStyle": {
                        "fontSize": 14
                    },
                    "borderWidth": 0,
                    "padding": 5
                }
            },
            "zlevel": 0,
            "z": 0
        }
    ],
    grid:{ // 让图表占满容器
        top:"20px",
        left:"30px",
        right:"20px",
        bottom:"35px"
        },

    // "legend": [
    //     {
    //         "data": [
    //             "\u5546\u5bb6A"
    //         ],
    //         "selected": {
    //             "\u5546\u5bb6A": true
    //         },
    //         "show": true,
    //         "padding": 5,
    //         "itemGap": 10,
    //         "itemWidth": 25,
    //         "itemHeight": 14
    //     }
    // ],
    "tooltip": {
        "show": true,
        "trigger": "item",
        "triggerOn": "mousemove|click",
        "axisPointer": {
            "type": "line"
        },
        "showContent": true,
        "alwaysShowContent": false,
        "showDelay": 0,
        "hideDelay": 100,
        "textStyle": {
            "fontSize": 14
        },
        "borderWidth": 0,
        "padding": 5
    },
    "xAxis": [
        {
            "show": false,
            "scale": false,
            "nameLocation": "end",
            "nameGap": 15,
            "gridIndex": 0,
            "inverse": false,
            "offset": 0,
            "splitNumber": 5,
            "minInterval": 0,
            "splitLine": {
                "show": false,
                "lineStyle": {
                    "show": true,
                    "width": 1,
                    "opacity": 1,
                    "curveness": 0,
                    "type": "solid"
                }
            },
            "data": xAxis
        }
    ],
    "yAxis": [
        {
            "show": true,
            "scale": false,
            "nameLocation": "end",
            "nameGap": 15,
            "gridIndex": 0,
            "inverse": false,
            // "type": "log",
            "offset": 0,
            "splitNumber": 5,
            "minInterval": 0,
            "splitLine": {
                "show": false,
                "lineStyle": {
                    "show": true,
                    "width": 1,
                    "opacity": 1,
                    "curveness": 0,
                    "type": "solid"
                }
            }
        }
    ],
    // "title": [
    //     {
    //         "text": "实时交易同步速度",
    //         "padding": 5,
    //         "itemGap": 10,
    //         "color": "gray"
    //     }
    // ],
    "dataZoom": {
        "show": true,
        "type": "slider",
        "realtime": true,
        "orient": "horizontal",
        // "zoomLock": true,
        "startValue": data.length  - 10,
        "endValue": data.length - 1,
        "filterMode": "filter",
        // "bottom":"auto"
    }
};
// chart_cd2d54c0a4684292ac7e8a2ff22e3996.clear()
        chart_cd2d54c0a4684292ac7e8a2ff22e3996.setOption(option_cd2d54c0a4684292ac7e8a2ff22e3996);
            },
            setRateOption(transactions) {
                let RateOption = {
    "animation": true,
    "animationThreshold": 2000,
    "animationDuration": 1000,
    "animationEasing": "cubicOut",
    "animationDelay": 0,
    "animationDurationUpdate": 300,
    "animationEasingUpdate": "cubicOut",
    "animationDelayUpdate": 0,
    "color": [
        "#c23531",
        "#2f4554",
        "#61a0a8",
        "#d48265",
        "#749f83",
        "#ca8622",
        "#bda29a",
        "#6e7074",
        "#546570",
        "#c4ccd3",
        "#f05b72",
        "#ef5b9c",
        "#f47920",
        "#905a3d",
        "#fab27b",
        "#2a5caa",
        "#444693",
        "#726930",
        "#b2d235",
        "#6d8346",
        "#ac6767",
        "#1d953f",
        "#6950a1",
        "#918597"
    ],
    "series": [
        {
            "type": "liquidFill",
            "name": "lq",
            "data": [
                (transactions.length + 900) / (transactions.length + 1000) 
            ],
            "waveAnimation": true,
            "animationDuration": 2000,
            "animationDurationUpdate": 1000,
            "color": [
                "#294D99",
                "#156ACF",
                "#1598ED",
                "#45BDFF"
            ],
            "shape": "circle",
            "backgroundStyle": {},
            "outline": {
                "show": true,
                "borderDistance": 8
            },
            "label": {
                "show": true,
                "position": "inside",
                "margin": 8,
                "fontSize": 25,
                "formatter": function (param) {   param.value + '%'; }
            },
        }
    ],
    "legend": [
        {
            "data": [],
            "selected": {},
            "show": true,
            "padding": 5,
            "itemGap": 10,
            "itemWidth": 25,
            "itemHeight": 14
        }
    ],
    "tooltip": {
        "show": true,
        "trigger": "item",
        "triggerOn": "mousemove|click",
        "axisPointer": {
            "type": "line"
        },
        "showContent": true,
        "alwaysShowContent": false,
        "showDelay": 0,
        "hideDelay": 100,
        "textStyle": {
            "fontSize": 14
        },
        "borderWidth": 0,
        "padding": 5
    },
    // "title": [
    //     {
    //         "text": "交易同步完成情况",
    //         "padding": 5,
    //         // "color": "lightgray",
    //         "itemGap": 10
    //     }
    // ]
}
                var chart_aed0d985ce754dc49f1a7bc9e343280f = echarts.init(
                    document.getElementById('rate'), 'white', {renderer: 'canvas'});
                    chart_aed0d985ce754dc49f1a7bc9e343280f.clear()
                chart_aed0d985ce754dc49f1a7bc9e343280f.setOption(RateOption)

            },
            updateAberration() {
                return this.aberration
            },
            getTransaction() {
                this.$http.get("/block/transaction/", { "params": {"index": this.transactions.length}}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function(res) {
                    if (res.body.transaction["tx_hash"] == this.transactions[this.transactions.length - 1]["tx_hash"])
                        return
                    this.dailyTx += 1
                    this.transactions.push(res.body.transaction)
                    this.setRateOption(this.transactions)
                    if (res.body.flag) {
                        console.log("检测到异常" + res.body.result)
                        this.$http.get("/block/abberration/", {"params": {"type": res.body.result}},  {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function(res){
                            this.aberration.push({
                                "index": this.aberration.length,
                                "source": res.body.source,
                                "account": res.body.account,
                                "value": res.body.value,
                                "tx": res.body.tx,
                                "label": res.body.label
                            })
                            this.change(this.transactions, this.aberration, this.blockNumber, this.blockTime)
                        })
                    }
                    this.change(this.transactions, this.aberration, this.blockNumber, this.blockTime)
                })

            }
        }
    }
</script>
<style>
