<template>
    <div style="width: 100%;">
      <div class="upload-content">
        <!-- <div class="step">
          <el-steps :active="this.activeNumber" align-center>
            <el-step title="上传文件"  icon="el-icon-upload"></el-step>
            <el-step title="完善查询表单" icon="el-icon-edit"></el-step>
            <el-step title="获得查询结果" icon="el-icon-star-on"></el-step>
          </el-steps>
        </div> -->
        <div class="upload-area">
          <el-upload
            class="upload"
            drag  
            action="/data/upload/"
            accept=".json"
            :limit="2"
            :show-file-list="false"
            multiple
            :on-exceed="handleExceed"
            :on-success="handleSuccess"
            :on-error="handleError"
            :before-upload="handleUpload"
            ref="upload"
            >
            <div style="margin: 0 auto;width: 80%;">
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
              <div class="el-upload__tip" slot="tip">只能上传edge.json和vertex.json</div>
            </div>
          </el-upload>
        </div>
        <div class="search-area">
          <div class="upload-info">
            <el-descriptions title="上传信息">
              <template slot="extra">
                <!-- <el-button type="primary" size="small" plain @click="newUpload">重新上传</el-button> -->
              </template> 
                <el-descriptions-item label="账户数"><el-tag size="small">{{ this.NodeNumber }}</el-tag></el-descriptions-item>
                <el-descriptions-item label="交易数"><el-tag size="small">{{ this.EdgeNumber }}</el-tag></el-descriptions-item>
                <el-descriptions-item label="区块范围"><el-tag size="small" type="primary">{{ this.blocknumber["min"] }}</el-tag>~<el-tag type="primary" size="small">{{this.blocknumber["max"]}}</el-tag></el-descriptions-item>
                <el-descriptions-item label="异常行为数">
                  <el-tag size="small" type="primary">{{ (this.labelNumber == 0) ? 0 : this.labelNumber - 1 }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item label="时间戳"><el-tag size="small" type="primary">{{ this.timestamp }}</el-tag></el-descriptions-item>
            </el-descriptions>
          </div>
          <!-- <div class="search-form">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item label="label">
                  <el-select v-model="labelSelect" placeholder="label" ref="Labelselect">
                    <el-option :label="l" :value="l" v-for="(l, index) in this.label" :key="index"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="search">查询</el-button>
                </el-form-item>
            </el-form>
          </div> -->
        </div>
      </div>
        <!-- <div style="width: 100%; text-align: center">
          <el-button type="primary" plain id="searchbutton" @click="search"><i class="fa fa-search"></i><span>查询</span></el-button>
        </div> -->
          <!-- <span class="hover-blue"><i class="el-icon-full-screen" @click="openProp"></i></span> -->
        <el-tabs v-model="activeName" style="margin-left: 2%;margin-top:4%;">
      <el-tab-pane label="异常" name="GraphTab" style="width: 100%">
        <div class="search-result">
          <div class="search-result">
          <div class="table-result">
            <TableComponent :data="aberration" :open="popper"></TableComponent>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    </div>
</template>
<script>
import TableComponent from "@/components/Table.vue"
// import EdgeInfo from "./components/EdgeInfo.vue";
import NetGraph from "@/components/NetGraph.vue";
export default ({
    name: "UploadComponent",
    props:{
        open: {
          type: Function,
          default: null
        },
        popper: {
          type: Function,
          default: null
        }
      },
    data() {
        return {
        activeName: 'GraphTab',
        fullshow: false,
        option4Graph: {},
        // edges4Table: [],
        uploading: false ,
        successNumber: 0, 
        edgeJson: false,
        VertexJson: false,
        labelNumber: 0,
        NodeNumber: 0,
        EdgeNumber: 0,
        // activeNumber: 1,
        labelSelect: '',
        // blocknumberLower: undefined,
        // blocknumberUpper: undefined,
        label: [],
        blocknumber: {
          "max": 'Unknown',
          "min": 'Unknown'
        },
        timestamp: '未上传',
        aberration:[]
        // EdgeInfoShow: false
        }
    },
    components: {
        TableComponent,
        NetGraph
    }, 
    mounted() {
    },
    methods: {
        openProp() {
          if (this.option4Graph != {} && this.option4Graph != undefined)
            this.popper(this.option4Graph, false)
        },
        search() {
        let label2Search = this.labelSelect
        // let blockNumberRange = [this.blocknumberLower, this.blocknumberUpper]
        // this.$refs.table.OpenLoading()
        //   setTimeout(() => {
        //   this.edges4Table = [
        //     {
        //         "index": 1,
        //         "source": "0x9cc2dce817093ceea82bb67a4cf43131fa354c06",
        //         "target": "0x5578840aae68682a9779623fa9e8714802b59946",
        //         "value": "0",
        //         "degrees": [
        //             "3",
        //             "107"
        //         ],
        //         "blocknumber": "13051232",
        //         "tx_hash": "0xb23924dde82c374edb3b05e92b0aa3004376a6f82fd9c94e0b752bd0223b8757",
        //         "total_name": [
        //             "0x9cc2dce817093ceea82bb67a4cf43131fa354c06",
        //             "0x5578840aae68682a9779623fa9e8714802b59946"
        //         ]
        //     }
        //   ]
        // }, 1000);
        this.$http.post('/data/timeline/', {label: label2Search}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function (res) {
                    // this.token = res.body.token
                    this.code = 200
                    this.activeNumber = 3
                    this.option4Graph = res.body.json
                    // this.edges4Table = res.body.edges
                    // this.fullshow = true
                    // setTimeout(() => {
                    //   this.$router.push({name: 'App', params: {token: this.token}})
                    //   this.$refs.fade_click.click()
                    // }, 1000)
                }, function (err) {
                    this.code = err.status
                    this.message = err.body.message
                })
        },
        openEdgeInfo(source, target, value, blocknumber, tx_hash) {
            this.open(source, target, value, blocknumber, tx_hash)
        },
        newUpload() {
          this.edgeJson = false
          this.VertexJson = false
        },
        handleSuccess(response, file) {
            this.showMessage('success')
            this.successNumber += 1
            if (file.name == "edge.json") {
              this.edgeJson = true
            } 
            if (file.name == "vertex.json") {
              this.VertexJson = true
            }
            if (this.successNumber == 2) {
              this.$http.get('/data/info/', {}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function (res) {
                    // this.token = res.body.token
                    this.code = 200
                    this.labelNumber = res.body.labelNumber
                    this.blocknumber = res.body.blocknumberRange
                    this.timestamp = res.body.timestamp
                    this.NodeNumber = res.body.NodeNumber
                    this.EdgeNumber = res.body.EdgeNumber
                    this.label = res.body.label
                    // setTimeout(() => {
                    //   this.$router.push({name: 'App', params: {token: this.token}})
                    //   this.$refs.fade_click.click()
                    // }, 1000)
                    this.successNumber = 0
                    this.VertexJson = false
                    this.edgeJson = false
                    this.$refs.upload.clearFiles()
                }, function (err) {
                    this.code = err.status
                    this.message = err.body.message
                    this.showMessage('error', err.body.message)
                    this.$refs.upload.clearFiles()
                    this.successNumber = 0
                    this.VertexJson = false
                    this.edgeJson = false
                })
                this.$http.get("/data/aberration/",{},{headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function(res) {
                    this.aberration = res.body.aberration
                }, function(err){
                    this.showMessage('error', err.body.message)
                })
            }
            console.log(response)
        },
        handleError(err) {
            this.showMessage('error', err)
            console.log(err)
        },
        handleExceed() {
          this.showMessage("exceed")
        },
        handleUpload(file) {
          if (this.uploading) {
            return false
          }
          if (file.name == "edge.json" && this.edgeJson) {
            this.showMessage("repeatError")
            this.$refs.upload.clearFiles()
            return false
          } 
          else if (file.name == "vertex.json" && this.VertexJson) {
            this.showMessage("repeatError")
            this.$refs.upload.clearFiles()
            return false
          } 
          else if (file.name != "edge.json" && file.name != "vertex.json"){
            this.showMessage("nameError")
            this.$refs.upload.clearFiles()
            return false
          }
          // console.log(file, fileList)
        },  
        showMessage(messageType, err) {
          if (messageType == 'exceed') {
            this.$notify({
              title: 'Error',
              message: 'Exceed Error: The number of files exceeds the limit',
              type: 'warning'
            });
          } 
          else if (messageType == 'success') {
            this.$notify({
              title: 'Success',
              message: 'Success: Upload Success',
              type: 'success'
            });
          }
          else if (messageType == 'error') {
            this.$notify.error({
              title: 'Error',
              message: err,
            })
          } 
          else if (messageType == 'nameError') {
            this.$notify.error({
              title: 'Error',
              message: 'Name Error: The name of file must be edge.json or vertex.json',
            })
          }
          else if (messageType == 'repeatError') {
            this.$notify.error({
              title: 'Error',
              message: "Repeat Upload Error: edge.json or vertex.json has been uploaded",
            })
          }
        }
  }
})
</script>
