<template>
    <div style="width: 100%">
        <el-empty description="No Data" style="width: 100%" v-show="empty"></el-empty>
        <div id="overview" style="width: calc(81vw);height: calc(66vh - 60px);" v-show="!empty"></div>
    </div>
</template>
<!-- <script type="text/javascript" src="/static/js/echarts.js"></script> -->
<script>
    export default {
        name: "OverviewGraph",
        props: ['option'],
        data() {
            return {
                empty: true,
                chart: undefined,
            }
        },
        mounted() {
        },
        watch: {

            option(newOption) {
                console.log(newOption)
                this.empty = false
                this.Draw(newOption["nodes"], newOption["edges"], newOption["dic"])
            }

            },
        methods: {
            Draw: function (newNodes, newEdges, dic) {
                if (this.chart != undefined) {
                    this.chart.destroy()
                }
                const globalFontSize = 12;
                const tooltip = new this.$antv.Tooltip({
                offsetX: 10,
                offsetY: 10,
                fixToNode: [1, 0.5],
                // the types of items that allow the tooltip show up
                // 允许出现 tooltip 的 item 类型
                itemTypes: ['node', 'edge'],
                // custom the tooltip's content
                // 自定义 tooltip 内容
                getContent: (e) => {
                    const outDiv = document.createElement('div');
                    outDiv.style.width = 'fit-content';
                    outDiv.style.height = 'fit-content';
                    const model = e.item.getModel();
                    if (e.item.getType() === 'node') {
                    outDiv.innerHTML = `${model.name}`;
                    } else {
                    const source = e.item.getSource();
                    const target = e.item.getTarget();
                    outDiv.innerHTML = `来源：${source.getModel().name}<br/>去向：${target.getModel().name}`;
                    }
                    return outDiv;
                },
                });
                let fixSelectedItems = {
                    fixAll: true,
                    fixState: 'yourStateName', // 'selected' by default
                    };
                    const fittingString = (str, maxWidth, fontSize) => {
                        const ellipsis = '...';
                        const ellipsisLength = this.$antv.Util.getTextSize(ellipsis, fontSize)[0];
                        let currentWidth = 0;
                        let res = str;
                        const pattern = new RegExp('[\u4E00-\u9FA5]+'); // distinguish the Chinese charactors and letters
                        str.split('').forEach((letter, i) => {
                            if (currentWidth > maxWidth - ellipsisLength) return;
                            if (pattern.test(letter)) {
                            // Chinese charactors
                            currentWidth += fontSize;
                            } else {
                            // get the width of single letter according to the fontSize
                            currentWidth += this.$antv.Util.getLetterWidth(letter, fontSize);
                            }
                            if (currentWidth > maxWidth - ellipsisLength) {
                            res = `${str.substr(0, i)}${ellipsis}`;
                            }
                        });
                        return res;
                        };
                const container = document.getElementById('overview')
                console.log(container)
                // const width = container.scrollWidth;
                // const height = container.scrollHeight;
                const width = 1300
                const height = 600
                const graph = new this.$antv.Graph({
                container: 'overview',
                width,
                height,
                plugins: [tooltip],
                modes: {
                        default: ['drag-canvas', 'activate-relations',
                        {
                            type: 'zoom-canvas',
                            fixSelectedItems,
                        },
                    ],
                        
                    },
                layout: {
                    type: 'force',
                    preventOverlap: true,
                    linkDistance: (d) => {
                    return 300;
                    },
                    nodeStrength: (d) => {
                    return -50;
                    },
                    // edgeStrength: (d) => {
                    // // if (newNodes[parseInt(d.source)]["jump"] <= 1) {
                    // //     return 0.7;
                    // // }
                    // return 10;
                    // },
                },
                defaultNode: {
                    stroke: '#aaa',
                    lineAppendWidth: 2,
                    opacity: 0.3,
                },
                labelCfg: {
                    style: {
                        fontSize: globalFontSize,
                    },
                    },
                // modes: {
                //     default: ['drag-canvas'],
                // },
                });

                const data = {
                nodes: newNodes,
                edges: newEdges,
                };
                data.nodes.forEach(function (node) {
                    node.label = fittingString(node.label, node.size, globalFontSize);
                    });
                const nodes = data.nodes;
                
                graph.data({
                nodes,
                edges: data.edges.map(function (edge, i) {
                    edge.id = 'edge' + i;
                    return Object.assign({}, edge);
                }),
                });
                graph.render();
                this.chart = graph
                graph.on('node:dragstart', function (e) {
                graph.layout();
                refreshDragedNodePosition(e);
                });
                graph.on('node:drag', function (e) {
                refreshDragedNodePosition(e);
                });
                graph.on('node:dragend', function (e) {
                e.item.get('model').fx = null;
                e.item.get('model').fy = null;
                });

                if (typeof window !== 'undefined')
                window.onresize = () => {
                    if (!graph || graph.get('destroyed')) return;
                    // if (!container || !container.scrollWidth || !container.scrollHeight) return;
                    if (!container) return
                    // graph.changeSize(container.scrollWidth, container.scrollHeight);
                    graph.changeSize(1300, 500)
                };

                function refreshDragedNodePosition(e) {
                const model = e.item.get('model');
                model.fx = e.x;
                model.fy = e.y;
                }
                        }
        },
        // mounted() {
        // }
    }
</script>
<style>
</style>