<template>
    <div>
      <!-- <el-button>异常行为分析</el-button> -->
    <el-table
      v-loading="loading"
      element-loading-text="Loading..."
      element-loading-spinner="el-icon-loading"
      :data="tableData.slice(this.page * this.pageSize - this.pageSize , this.page * this.pageSize)"
      :header-cell-style="{'text-align': 'center'}"
      :cell-style="{'text-align': 'center'}"
      style="width: 100%;min-height: calc(48vh); max-height:calc(48vh); overflow: auto;">
      <el-table-column
        prop="index"
        width="100" label="Index">
      </el-table-column>
      <el-table-column
      prop="tag"
      label="From Account" width="320">
      <template slot-scope="scope">
        <el-tag
          type='primary'
          disable-transitions>{{scope.row.source}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      prop="tag"
      label="To Accounts" width="320">
      <template slot-scope="scope">
        <el-tag
          type='success'
          disable-transitions>{{scope.row.target}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
        prop="value"
        label="Value"
        width="200">
      </el-table-column>
      <el-table-column
      prop="tag"
      label="Timestamp" width="180">
      <template slot-scope="scope">
        <el-tag
          type='warning'
          disable-transitions>{{scope.row.timestamp}}</el-tag>
      </template>
    </el-table-column>
      <el-table-column
        width="90"
        label="Overview "
        >
        <template slot-scope="scope">
        <el-button plain id="searchbutton" size="mini" @click="openChild($event, scope.row.label)"><span><i class="fa fa-eye"></i></span></el-button>
      </template>
      </el-table-column>
    </el-table>
    <el-pagination
  background
  @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[6, 12, 24]"
      :page-size="this.pageSize"
      :total="this.tableData.length"
      layout="total, sizes, prev, pager, next, jumper">
</el-pagination>
    </div>
  </template>
  
  <script>
    export default {
      name: "TableComponent",
      props:{
        open: {
          type: Function,
          default: null
        },
        update: {
          type: Function,
          default: null
        },
        data: {
          type: Array,
          default: function() {
            return []
          }
        }
      },
        watch: {
            data(newData) {
              // console.log(newData)
              this.page = 1
              // newData["index"] = this.tableData.length + 1
              this.tableData = newData
              this.loading = false
            },

            },
      methods: {
        deleteRow(index, rows) {
          rows.splice(index, 1);
        },
        handleCurrentChange(val) {
            this.page = val
        },
        handleSizeChange(val) {
          this.pageSize = val
        },
        OpenLoading() {
          this.loading = true
        },
        openChild(event, label) {
          // console.log(index)
          // let edge = this.tableData[parseInt(index) - 1]
        //   let edge =         {
        //     "source": 0,
        //     "target": 1,
        //     "value": "0",
        //     "degrees": [
        //         "3",
        //         "107"
        //     ],
        //     "blocknumber": "13051232",
        //     "tx_hash": "0xb23924dde82c374edb3b05e92b0aa3004376a6f82fd9c94e0b752bd0223b8757",
        //     "total_name": [
        //         "0x9cc2dce817093ceea82bb67a4cf43131fa354c06",
        //         "0x5578840aae68682a9779623fa9e8714802b59946"
        //     ]
        // }
          // if (this.open) {
          //   let source = {
          //     "address": edge["source"],
          //     "degree": edge["degrees"][0]
          //   }
          //   let target = {
          //     "address": edge["target"],
          //     "degree": edge["degrees"][1]
          //   }
          //   let value = edge["value"]
          //   let blocknumber = edge["blocknumber"]
          //   let tx_hash = edge["tx_hash"]
            this.$http.post('/data/search/', {label: label, blockNumberRange:[0, -1]}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function(res){
              this.open(res.body.json, true)
            })
          // }
        }
      },
      mounted(){
        this.tableData = this.update()
        this.timer = setInterval(()=>{
          this.tableData = this.update()
        }, 1000)
      },
      destroyed() {
        if (this.timer != undefined) clearInterval(this.timer)
      },
      data() {
        return {
          page: 1,
          timer: undefined,
          tableData: [
            {
              "source": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "target": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "value": 6897.690318814533,
    "timestamp": "2023-02-27 16:27:48",
    "index": 0
            },
            {
              "source": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "target": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "value": 6897.690318814533,
    "timestamp": "2023-02-27 16:27:48",
    "index": 1
            },
            {
              "source": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "target": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "value": 6897.690318814533,
    "timestamp": "2023-02-27 16:27:48",
    "index": 2
            },
            {
              "source": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "target": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "value": 6897.690318814533,
    "timestamp": "2023-02-27 16:27:48",
    "index": 3
            },
            {
              "source": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "target": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "value": 6897.690318814533,
    "timestamp": "2023-02-27 16:27:48",
    "index": 4
            },            {
              "source": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "target": "0xaf9bdc92c920415cbcb8572a2dcb8aade778312b",
    "value": 6897.690318814533,
    "timestamp": "2023-02-27 16:27:48",
    "index": 5
            }
          ],
          loading: false,
          pageSize: 6
        }
      }
    }
  </script>