        </div>
        <div class="document-why">
            <div class="content">
                以上 187 起安全事件中，攻击手法主要分为四类：由项目自身设计缺陷和各种合约漏洞引起的攻击；包含 Rug Pull、钓鱼攻击等手法的 Scam；由于私钥泄露引起的资产损失；前端恶意攻击，这四种主要攻击手法占比安全事件总数量的 95%。<br>
                上半年由项目自身设计缺陷和各种合约漏洞引起的攻击共92起，造成损失10.6亿美元，其中利用闪电贷引起的攻击有19起，造成损失6133万美元。因私钥被盗引起的资产损失发生率约为4%，损失金额却达到7.2亿美元。
            </div>
        </div>
        <div class="example">
