            </div>
        </div>
        <div class="content">
            尽管 2022 年区块链技术正在飞速发展并且逐渐完善，但层出不穷的加密货币攻击事件对区块链生态安全态势提出了新的挑战。
        </div>
            <div class="sub-title">提出的方案</div>
            <div class="document-what">
                <div class="content">
                    针对上述痛点，我们提出了面向“区块链+监管”的即席图分析系统，本系统通过设计快速的读写解耦合的数据拉取机制、高效的时序数据存储方法，以及精巧的图数据算子，可以很好地满足监管方对任意时间窗口的区块链数据做高效的查询和分析的需求。
                </div>
            </div>
            <div style="width: 80%; margin: 0 auto; text-align:center">
