                    本系统可被应用于面向区块链账本数据的以反洗钱和反欺诈为目的的金融领域中典型的数据分析场景，创新点如下：
                </div>
                <div style="margin-left: 5%;font-size: large"><li>快速的读写解耦合的数据拉取机制</li></div>
                <div class="content">通过并行拉取和乱序解析将链上数据快速同步至链下进行图建模，提高了链上数据链下同步效率。</div><br>
                <div style="margin-left: 5%;font-size: large;"><li>优化的时序数据存储方法</li></div>
                <div class="content">基于链上数据的时序特征，设计了结合区块时序特征的可合并的分段图存储引擎，在保留快速写入性能的同时提高了数据的顺序访问效率。</div><br>
                <div style="margin-left: 5%;font-size: large;"><li>高效的图数据查询能力</li></div>
